import { Injectable } from '@angular/core';
import { forkJoin, Observable, of } from 'rxjs';
import { tap } from 'rxjs/operators';
import { UserDto } from '../constants/common.entities';
import { HttpService } from './http.service';
import { AuthState } from './auth.state';
import { AuthURL } from '../urls/rbac-url.config';
import { Store } from './store.service';

@Injectable()
export class AuthStore extends Store<AuthState> {
  constructor(private httpService: HttpService) {
    super(new AuthState());
  }

  addAuthInfo(user: UserDto): void {
    this.setState({ ...this.state, user });
  }

  getAuthInfo(): Observable<any> {
    console.log(this.state);
    if (this.state.user) {
      return of(this.state.user);
    } else {
      return forkJoin([this.httpService.get(AuthURL.EndPoints.auth.user.userInfo)]).pipe(
        tap(([user]) => {
          return user;
        })
      );
    }
  }
}
