
import { routerReducer, RouterReducerState } from '@ngrx/router-store';
import { ActionReducerMap, createFeatureSelector, MetaReducer } from '@ngrx/store';
import { authReducer } from './auth.reducer';

import { AuthState } from '../constants/common.entities';
import { initStateFromLocalStorage } from './init-state-from-local-storage.reducer';
import { RouterStateUrl } from './router.state';
import { debug } from './debug.reducer';


export const reducers: ActionReducerMap<AppState> = {
  auth: authReducer,
  router: routerReducer
};

export const metaReducers: MetaReducer<AppState>[] = [initStateFromLocalStorage];

// if (environment['production']) {
  metaReducers.unshift(debug);
// }

export const selectAuthState = createFeatureSelector<AuthState>('auth');

export const selectRouterState = createFeatureSelector<RouterReducerState<RouterStateUrl>>('router');

export interface AppState {
  auth: AuthState;
  router: RouterReducerState<RouterStateUrl>;
}

export interface State extends AppState {
  app: AppState;
}
