import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AppService {
  currentState: Subject<any> = new Subject();
  currentValue = new BehaviorSubject<any>({});
  getValue = this.currentValue.asObservable();
  addValue(key: string, value: string) {
    this.currentState.next({ key, value });
  }

  setValue(key: string, value: string) {
    this.currentValue.next({ key, value });
  }
}
